html,
body {
	// scroll-behavior: smooth !important;
}

html::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f5;
}

html::-webkit-scrollbar {
    width: 10px;
    background-color: #f5f5f5;
}

html::-webkit-scrollbar-thumb {
    background-color: var(--theme-color);
    background-image: -webkit-linear-gradient(
        45deg,
        rgba(255, 255, 255, 0.2) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.2) 50%,
        rgba(255, 255, 255, 0.2) 75%,
        transparent 75%,
        transparent
    );
}

body {
	font-family: $body-font;
	font-size: $body-font-size;
	font-weight: $body-font-weight;
	color: $body-color;
	line-height: $body-line-Height;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
}

iframe {
	border: none;
	width: 100%;
}
input, textarea {
	font-family: $para-font;
}
.slick-slide:focus,
button:focus,
a:focus,
a:active,
input,
input:hover,
input:focus,
input:active,
textarea,
textarea:hover,
textarea:focus,
textarea:active {
	outline: none;
}


input:focus {
	outline: none;
	box-shadow: none;
}

img:not([draggable]),
embed,
object,
video {
	max-width: 100%;
	height: auto;
}

ul {
	list-style-type: disc;
}

ol {
	list-style-type: decimal;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
	// border: 1px solid $border-color;

}

th {
	font-weight: 700;
	color: $title-color;
}

td,
th {
	border: 1px solid $border-color;
	padding: 9px 12px;
}


a {
	color: $theme-color;
	text-decoration: none;
	outline: 0;
	transition: all ease 0.4s;

	&:hover {
		color: $title-color;
	}

	&:active,
	&:focus,
	&:hover,
	&:visited {
		text-decoration: none;
		outline: 0;
	}
}




img {
	border: none;
	max-width: 100%;
}

ins {
	text-decoration: none;
}

pre {
	background: #f5f5f5;
	color: #666;
	font-size: 14px;
	margin: 20px 0;
	overflow: auto;
	padding: 20px;
	white-space: pre-wrap;
	word-wrap: break-word;
}

span.ajax-loader:empty,
p:empty {
	display: none;
}


p {
	margin: 0 0 22px 0;
	color: $body-color;
	line-height: $p-line-Height;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
p a,
span a {
	font-size: inherit;
	font-family: inherit;
	font-weight: inherit;
	line-height: inherit;
}


.h1, h1,
.h2, h2,
.h3, h3,
.h4, h4,
.h5, h5,
.h6, h6 {
	font-family: 'Inter';
font-style: normal;
	color: $title-color;
	text-transform: none;
	font-weight: 600;
	line-height: 1.3;
	margin: 0 0 15px 0;
	display: block;
	text-transform: uppercase;
}

.h1,
h1 {
	font-size: 56px;
	line-height: 1.25;
}

.h2,
h2 {
	font-size: 36px;
	line-height: 1.35;
}

.h3,
h3 {
	font-size: 32px;
	line-height: 1.4;
}

.h4,
h4 {
	font-size: 24px;
	line-height: 1.5;
}

.h5,
h5 {
	font-size: 20px;
}

.h6,
h6 {
	font-size: 18px;
}

@include lg {
	.h1, 
	h1 {
		font-size: 40px;
	}

	.h2,
	h2 {
		font-size: 30px;
	}

	.h3,
	h3 {
		font-size: 24px;
	}

	.h4,
	h4 {
		font-size: 22px;
	}
}

@include sm {
	.h1, 
	h1 {
		font-size: 30px;
	}
	
	.h2, 
	h2 {
		font-size: 28px;
	}
	
	.h3, 
	h3 {
		font-size: 22px;
	}

	.h4,
	h4 {
		font-size: 20px;	
	}
	.h5,
	h5 {
		font-size: 18px;	
	}
}

@include xs {
	.h1, 
	h1 {
		font-size: 28px;
	}
	.h2, 
	h2 {
		font-size: 24px;
	}
}