.featured-img {
    img {
        width: 100%;
    }
}
.featured-title {
    margin-bottom: 25px;
}
.featured-text {
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 35px;
}
.featured-slide-area {
    position: relative;
    .slider-nav-wrap {
        position: absolute;
        bottom: -52px;
        left: 50%;
        transform: translateX(-50%);
      
    }
}

// @media (min-width: 1920px) {


// }

@media (min-width: 992px) {
    .featured-slide-area {
        .slider-nav-wrap {
            width: 100%;
            .slick-arrow {
                color: $title-color;
            }
            .slick-dots button {
                --body-color: #000;
            }
        }
    }
}

.slider-nav-wrap {
    // background-color: rgba($color: #8E8E8E, $alpha: 0.7);
    padding: 17px 0px;
}
.slider-nav {
    display: flex;
    
    align-items: center;
    gap: 40px;
    justify-content: space-between;
    .slick-dots {
        margin: 0;
    }
}

@include md {
    .featured-title {
        // display: none;
    }
    .featured-text {
        display: none;
    }
    .featured-img {
        margin-bottom: 10px;
    }
    .featured-slide-area {
        .slider-nav-wrap {
            bottom: -33%;
            left: unset;
            right: 0;
            transform: translateX(0);
            background-color: transparent;
            padding: 0;
            z-index: 1;
        }
    }
}

@include xs {
    .slider-nav {
        gap: 15px;
        .slick-dots li {
            margin:0 5px;
        }
    }
}

@include vxs {
    .slider-nav {
        gap: 6px;
    }
}



