.product-title {
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 15px;
    a {
        color: inherit;
        &:hover {
            color: $theme-color;
        }
    }
    @include xs {
        font-size: 14px;
    }
    &.style2 {
        font-size: 20px;
        @include md {
            font-size: 20px;
        }
        @include xs {
            font-size: 14px;
        }
    }
}
.ot-product {
    text-align: center;
    .product-img {
        margin-bottom: 26px;
        position: relative;
        .tag {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            white-space: nowrap;
        }
        .tagtwo {
            position: absolute;
            top: 103%;
            left: 48px;
            transform: translate(-50%, -50%);
            white-space: nowrap;
            @include xs {

                font-size: 11px;
            }
        }
    }
    .product-content {
        padding: 0 10px;
    }
    .price {
        display: block;
        font-size: 16px;
        font-weight: 600;
        color: $theme-color;
        margin-bottom: -0.3em;
    }
    &.sold-out {
        .product-img {
            img {
                opacity: 0.4;
            }
        }
    }
    &.upcoming {
        .product-img {
            img {
                opacity: 0.4;
            }
        }
        .ot-btn {
          
            line-height: 1.2;
        }
        .price{
            display: none;
        }
    }
}

@include xs {
    .ot-product {
        .product-img {
            .tag{
                font-size: 10px;
            }
            .tag.tagtwo {
                font-size: 9px;
            }
        }
    }
}

