.recommend-product {

    transition: 0.4s ease-in-out;
    .product-img {
        transition: 0.4s ease-in-out;
        margin-bottom: 35px;
        max-width: 100%;
        
      position: relative;
      
      margin-bottom: 35px;
      max-width: 100%;
      overflow: hidden;

a{

    width: 50%;
}

      img{
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
      .img_swap {
        height: 100%;
        width: 100%;
    }

    }
    .product-title {
        margin-bottom: 20px;
    }
}

