//-------------------
.filter-tab {
    margin-right: -15px;
    text-align: right;
    .tab-btn {
        border: none;
        padding: 0;
        background-color: transparent;
        font-size: 20px;
        font-weight: 600;
        text-transform: uppercase;
        margin: 0 15px;
        &.active {
            color: $theme-color;
        }
    }
}
.filter-menu {
    text-align: right;
    .filter-btn {
        border: 1px solid;
        padding: 7px 17px;
        background-color: transparent;
        font-size: 16px;
        font-weight: 600;
        text-transform: uppercase;
    }
}

@media (min-width: 992px) {
    .filter-menu {
        display: none;
    }
}

@include md {
    .filter-tab {
        display: none;
    }
}

// Product Details -----------------------------
.product-big-img {
    margin-bottom: 50px;
}
.product-thumb-tab {
    display: flex;
    gap: 20px;
    .tab-btn {
        width: 128px;
        cursor: pointer;
    }
}

.product-about {
    .price {
        font-size: 20px;
        font-weight: 600;
        color: $theme-color;
    }
}

.quantity-wrap {
    display: flex;
    align-items: center;
    gap: 10px 50px;
    border-top: 1px solid $border-color;
    padding: 40px 0;
} 
.qty-btn {
    border: 2px solid;
    background-color: transparent;
    border-radius: 3px;
}
.qty-input {
    border: none;
    text-align: center;
    width: 72px;
    font-size: 18px;
    font-weight: 600;
    -moz-appearance: textfield;
}
.qty-input::-webkit-outer-spin-button,
.qty-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.product-content {
    margin-top: 40px;
    text-transform: uppercase;
    p {
        font-size: 16px;
    }
    ul {
        padding-left: 17px;
        li {
            margin-bottom: 8px;
        }
    }
}

@include sm {
    .product-about {
        margin-top: 40px;
    }
}


