.img_swap {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    transition: 0.4s ease-in-out;
}


.product-img:hover .img_swap {
    visibility: visible;
    opacity: 1;
    height: 100%;
    object-fit: cover;
}






.card-wrapper:hover .media.media--hover-effect > img + img {
    opacity: 1;
 transform: scale(1) !important;
    transition: 0.4s ease-in-out !important;
}



.hello.slick-arrow {
    display: inline-block;
    padding: 0;
    background-color: transparent;
    position: absolute;
    top: 50%;
    margin-right: 50px;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    border: none;
    left: var(--pos-x,-85px);
    width: var(--icon-size,40px);
    height: var(--icon-size,40px);
    line-height: var(--icon-size,38px);
    border-radius: 999px;
    z-index: 2;
    color: var(--title-color);
    text-align: center;
     

    @include xl {
        left: var(--pos-x,-40px);

    }
    @include ml {
        display: none;

    }
    @include sm {


        position: unset;
        display: none !important;
    }
}
.hello.slick-arrow.next-arrow{
    margin-right: 0;
    margin-left: 50px;
    right: var(--pos-x,-85px);
    left: auto;

    @include xl {
        right: var(--pos-x,-40px);

    }
    @include ml {
        display: none;

    }
    
}
button.hello.slick-arrow img {
    width: 21px;
}
.filter-menu {
@include ml {
    .slick-slide.slick-current.slick-active {
        display: none;
    }


}
}







