.card {
    border: none;
  }
  
  .page-width {
    max-width: 1329px;
  
  }
  .collection-hero__title {
    margin: 2.5rem 0;
    text-align: center;
}
.facets-container {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, auto);
    padding-top: 1rem;
    padding-bottom: 1rem;
}


  html {
    height: -webkit-fill-available;
  }
