@import 'spacing/spacing';



body{

    -webkit-backface-visibility: hidden;
    -webkit-transform:translate3d(0,0,0);
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    transform-style: preserve-3d;
    -webkit-transform-style: preserve-3d
};


svg.icon.icon-cart {
    display: none;
}


a.img_swap img {
    height: 100%;
    object-fit: cover;
}


