
@media (min-width: 992px) {
  .row {
    --bs-gutter-x: 20px;
    &.gx-0 {
      --bs-gutter-x: 0;
    }
  }
}
.g-10,
.gx-10 {
  --bs-gutter-x: 10px;
}
.g-10,
.gy-10 {
  --bs-gutter-y: 10px;
}

.g-50,
.gy-50 {
  --bs-gutter-y: 50px;
}

.g-40,
.gy-40 {
  --bs-gutter-y: 40px;
}
