.subscribe-sec {
	background-color: $smoke-color;
	padding: 70px 0;

	@include xl {

		padding: 60px 0;

	}
}
.footer-wrapper {
	background-color: $title-color;
	--body-color: #fff;
	overflow: hidden;
	.ot-social {
		a {
			font-size: 22px;
		}
	}
	.widget-area {
		padding: 60px 0;
		position: relative;
	}
	.footer-logo {
		margin-bottom: 25px;
		max-width: 250px;
		display: inline-block;
	}
}
.footer-right {
	text-align: right;
}
.footer-menu {
	margin-bottom: 0px;
	ul {
		list-style-type: none;
		padding-left: 0;
		margin: 0;
	}
	li {
		display: inline-block;
		margin-right: 35px;
		// &:last-child {
		// 	margin-right: 0;
		// }
	}
	a {
		color: $white-color;
		font-size: 20px;
		text-transform: uppercase;
	}
}
.payment-methods {
	display: flex;
	justify-content: flex-end;
	gap: 20px;
}
.sub-menu.ot-submenu  li {
	 position: relative;
}
.sub-menu.ot-submenu  li:before {
	content: '\2014';
	position: absolute;
	margin-left: -18px;
    top: 30%;

    transform: translateY(-17%);

  }
  .menu-item-hot-children.ot-item-hot-children.ot-active ul{
	margin-left: 10px;

  }
.copyright {
	text-transform: uppercase;
	margin-bottom: 0;
	padding: 30px 0;
	text-align: center;
	font-size: 16px;
	font-weight: 500;
	color: $light-color;
	border-top: 1px solid $border-color;
}

@include lg {
	.footer-left {
		text-align: center;
		margin-bottom: 50px;
	}
	.footer-wrapper {
		.ot-social {

			width: 100%;
			justify-content: center;
		}
		.payment-methods {
				display: flex;
	justify-content: center;
	gap: 20px;
		}
	}
	.footer-right {
		text-align: left;
	}
	.footer-menu {
		&-wrap {
			display: flex;
			justify-content: center;
		}

	}

	.payment-methods {

		justify-content: center;

	}

}


@include md {

	.footer-menu {
		&-wrap {
			display: flex;
			// justify-content: space-between;
		}
		li {
			display: block;
			margin-bottom: 27px;
			margin-right: 0;
		}
		a {
			font-size: 16px;
		}
	}

	.payment-methods {
		flex-wrap: wrap;
		justify-content: space-around !important;
	}

}



.footer-menu {
    /* margin-bottom: 35px; */
}

@media only screen and (max-width: 1350px) and (min-width: 1200px)  {

	.footer-menu {
		 margin-bottom: 0px; 
	}

}
@media only screen and (min-width: 1200px) {
	.payment-all-wrapper {
		max-width: 0px;
		display: inline-block;
	}

	}


	@media (min-width: 300px) and (max-width: 450px) { 
		li.list-payment__item {
			width: 20px;
		}
		li.list-payment__item svg {
			width: 25px;
		}

	}
	@media (min-width: 800px) and (max-width: 1030px) { 
		li.list-payment__item {
			width: 25px;
		}
		li.list-payment__item svg {
			width: 31px;
		}
		.footer-wrapper .widget-area {
			padding: 44px 0;
			position: relative;
		}
		.footer-menu a {
	
			font-size: 18px;
		
		}
		.footer-left {

			margin-bottom: 36px;
		}
		.copyright {

			padding: 22px 0;

			font-size: 15px;
			
		}
	}
	@media (min-width: 500px) and (max-width: 799px) { 
		li.list-payment__item {
			width: 25px;
		}
		li.list-payment__item svg {
			width: 31px;
		}
		.footer-wrapper .widget-area {
			padding: 44px 0;
			position: relative;
		}
		.footer-menu a {
	
			font-size: 17px;
		
		}
		.footer-left {

			margin-bottom: 36px;
		}
		.copyright {

			padding: 22px 0;

			font-size: 15px;
			
		}
		.footer-menu li {
			display: inline-block;
			margin-bottom: 0px;
			margin-right: 0;
			padding: 0 20px;
		}
		.payment-all-wrapper {
			display: inline-block;
			
		}
		.footer-right{
            text-align: center;
		}
		.ot-social.ot-social2 a svg {
			width: 18px;
			height: 18px;
		}
	}
	@media (min-width: 299px) and (max-width: 499px) { 
		li.list-payment__item {
			width: 18px;
		}
		li.list-payment__item svg {
			width: 28px;
		}
		.footer-wrapper .widget-area {
			padding: 44px 0;
			position: relative;
		}
		.footer-menu a {
	
			font-size: 14px;
		
		}
		.footer-left {
			margin-bottom: 21px;
		}
		.copyright {
			padding: 7px 0;
			font-size: 10px;
		}
		.footer-menu li {
			display: inline-block;
			margin-bottom: 0px;
			margin-right: 0;
			padding: 0 7px;
		}
		.payment-all-wrapper {
			display: inline-block;
			
		}
		.footer-right{
            text-align: center;
		}
		.ot-social.ot-social2 a svg {
			width: 18px;
			height: 16px;
		}
		li.list-payment__item {
			width: 10px;
			height: 18px;
		}
		.footer-wrapper .footer-logo {
			margin-bottom: 11px;
		}
		.ot-social {
			gap: 15px;
		}
	}

