.popup-search-box {
  position: fixed;
  top: 0;
  left: 50%;
  background-color: rgba(0, 0, 0, 0.95);
  height: 0;
  width: 0;
  overflow: hidden;
  z-index: 99999;
  opacity: 0;
  visibility: hidden;
  border-radius: 50%;
  transform: translateX(-50%);
  transition: all ease 0.4s;


  button.searchClose {
    width: 60px;
    height: 60px;
    line-height: 1;
    position: absolute;
    top: 40px;
    right: 40px;
    border-width: 1px;
    border-style: solid;
    border-color: $white-color;
    background-color: transparent;
    font-size: 22px;
    border-radius: 50%;
    transform: rotate(0);
    transition: all ease 0.4s;
 i{
   color: $white-color;

 }

    &:hover {
      color: $body-color;
      background-color: #fff;
      border-color: transparent;
      border-color: transparent;
      transform: rotate(90deg);
       i{
        color: $body-color;
      }
    }
  }

  form {
    position: absolute;
    top: 50%;
    left: 50%;
    display: inline-block;
    padding-bottom: 40px;
    cursor: auto;
    width: 100%;
    max-width: 700px;
    transform: translate(-50%, -50%) scale(0);
    transition: transform ease 0.4s;

    @include lg {
      max-width: 600px;
      
    }
    @include sm {
      text-align: center;

    }

    input {
      font-size: 18px;
      height: 70px;
      width: 100%;
      border: 2px solid ;
      border-color: $white-color;
      background-color: transparent;
      padding-left: 30px;
      padding-right: 30px;
      color: #fff;
      border-radius: 50px;

      @include inputPlaceholder {
        color: #fff;
      }
      @include sm {
        width: 94%;
  
      }
    }

    // button {
    //   position: absolute;
    //   top: 0px;
    //   background-color: transparent;
    //   border: none;
    //   color: #fff;
    //   font-size: 24px;
    //   right: 12px;
    //   color: $white-color;
    //   cursor: pointer;
    //   width: 70px;
    //   height: 70px;
    //   line-height: 1;
    //   transition: all ease 0.4s;
    //   transform: scale(1.001);
      
    //   &:hover {
    //     transform: scale(1.1);
    //   }
    // }
    button.predictive-search__item {
      background: #fff;
     }
  }

  &.show {
    opacity: 1;
    visibility: visible;
    width: 100.1%;
    height: 100vh;
    transition: all ease 0.4s;
    border-radius: 0;

    form {
      transition-delay: 0.5s;
      transform: translate(-50%, -50%) scale(1);
    }
  }

}






.popupsearchhere{


  position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
  padding-bottom: 40px;
  cursor: auto;
  width: 100%;
  max-width: 700px;
  -webkit-transform: translate(-50%,-50%) scale(0);
  -ms-transform: translate(-50%,-50%) scale(0);
  transform: translate(-50%,-50%) scale(0);
  -webkit-transition: -webkit-transform ease .4s;
  transition: -webkit-transform ease .4s;
  -o-transition: transform ease .4s;
  transition: transform ease .4s;
  transition: transform ease .4s,-webkit-transform ease .4s;
}


